 
<template>
    <!-- 单选 -->
    <a-radio-group v-if="tagType == 'radio'" @change="handleInput" :value="getValueSting" :disabled="disabled">
        <a-radio v-for="(item, key) in dictOptions" :key="key" :value="valueFeild ? item[valueFeild] : item.value"
            >{{ getLabel(item) }}
        </a-radio>
    </a-radio-group>
    <!-- 单选按钮 -->
    <a-radio-group
        v-else-if="tagType == 'radioButton'"
        buttonStyle="solid"
        @change="handleInput"
        :value="getValueSting"
        :disabled="disabled"
    >
        <a-radio-button
            v-for="(item, key) in dictOptions"
            :key="key"
            :value="valueFeild ? item[valueFeild] : item.value"
            >{{ getLabel(item) }}</a-radio-button
        >
    </a-radio-group>
    <!-- 下拉单选 -->
    <a-select
        :showSearch="showSearch"
        style="width: 100%"
        v-else-if="tagType == 'select'"
        :getPopupContainer="getPopupContainer"
        :placeholder="placeholder"
        :disabled="disabled"
        @change="handleInput"
        :value="value"
        :allowClear="allowClear"
        :filter-option="filterOption"
    >
        <a-select-option
            v-for="(item, key) in dictOptions"
            :key="key"
            :value="valueFeild ? item[valueFeild] : item.value"
            :label="valueFeild ? item[valueFeild] : item.value"
            :disabled="item.disabled"
        >
            <span style="display: inline-block; width: 100%" :title="item.text || item.label">
                {{ getLabel(item) }}
            </span>
        </a-select-option>
    </a-select>
</template>

<script>
import { getObjectByString } from '../utils/util'
// import { getDictItemsFromCacheAsync } from '@/services/common.js'
export default {
    name: 'DictSelectTag',
    props: {
        filterOption: {
            type: [Boolean, Function],
            default: false
        },
        dictCode: String,
        placeholder: {
            type: String,
            default: '请选择'
        },
        triggerChange: Boolean,
        disabled: Boolean,
        value: [String, Number],
        type: String,
        getPopupContainer: {
            type: Function
            // default: (node) => node.parentNode
        },
        dataList: Array,
        valueFeild: String,
        textFeild: String,
        textFeildInterval: String,
        url: '',
        dataType: {
            type: String,
            default: 'code'
        },
        params: {
            type: String
        },
        method: {
            type: String,
            default: 'post'
        },
        showSearch: {
            type: Boolean,
            default: false
        },
        allowClear: {
            type: Boolean,
            default: true
        },
        getDataType: {
            type: String
        }
    },
    data() {
        return {
            dictOptions: [],
            tagType: ''
        }
    },
    watch: {
        // 请求方式
        dataType: {
            deep: true,
            immediate: true,
            handler(newVal, oldVal) {
                if (newVal === 'static') {
                    this.dictOptions = this.dataList
                    // console.log('选项 =>', this.dictOptions)
                } else if (newVal === 'dynamic') {
                    this.getDataList()
                } else {
                    this.initDictData()
                }
            }
        },
        // 静态数据
        dataList: {
            deep: true,
            immediate: true,
            handler(newVal, oldVal) {
                this.dictOptions = newVal
            }
        }
    },
    created() {
        if (!this.type || this.type === 'list') {
            this.tagType = 'select'
        } else {
            this.tagType = this.type
        }
    },
    computed: {
        getValueSting() {
            // console.log('this.value =>', this.value)
            // console.log('this.dictOptions =>', this.dictOptions)
            // update-begin author:wangshuai date:20200601 for: 不显示placeholder的文字 ------
            // 当有null或“” placeholder不显示
            return this.value != null ? this.value : undefined
            // update-end author:wangshuai date:20200601 for: 不显示placeholder的文字 ------
        }
    },
    methods: {
        getLabel(item) {
            if (this.textFeild) {
                let labelArr = []
                let split = this.textFeild.split(',')
                for (let it of split) {
                    if (item[it] != null && item[it] != undefined) {
                        labelArr.push(item[it])
                    }
                }
                //console.log("getLabel:"+labelArr)
                return labelArr.join(this.textFeildInterval ?? ',')
            } else {
                //console.log("getLabel2:"+item.label || item.text)
                return item.label || item.text
            }
        },
        // 获取动态数组
        getDataList() {
            if (this.getDataType === 'custom') {
                let params = JSON.parse(this.params)
                this.$axios(this.method, this.url, params).then((res) => {
                    if (res.statusCode !== 300) {
                        if (res instanceof Array) {
                            this.dictOptions = res
                        }
                        if (res.rows instanceof Array) {
                            this.dictOptions = res.rows
                        }
                    }
                })
            } else {
                let params = getObjectByString(this.params, ',', ':')
                this.$axios(this.method, this.url, params).then((res) => {
                    if (res.statusCode !== 300) {
                        this.dictOptions = res
                    }
                })
            }
        },
        // 获取字典数据
        initDictData() {
            return
            getDictItemsFromCacheAsync(this.dictCode).then((res) => {
                this.dictOptions = res
            })
            // this.$post('/dealersystem/dicSet/getDicItemByCode', { code: this.dictCode }, null).then((res) => {
            //       if (res) {
            //           this.dictOptions = res
            //       }
            //   })
        },
        // 输入框输入
        handleInput(e) {
            let val
            if (this.tagType == 'radio') {
                val = e.target.value
            } else {
                val = e
            }
            this.$emit('change', val)
        },
        setCurrentDictOptions(dictOptions) {
            this.dictOptions = dictOptions
        },
        getCurrentDictOptions() {
            return this.dictOptions
        }
    },
    model: {
        prop: 'value',
        event: 'change'
    }
}
</script>

<style scoped></style>
