 
<template>
    <div class="DataSearch">
        <button class="cu-btn-search" @click.stop.prevent="handleSearch">
            <a-icon type="search" style="font-size: 13px" />
            筛选
            <span @click.stop.prevent="delData" class="btn_delIcon" v-show="dataLen > 0 ? true : false">
                {{ dataLen }}
                <a-icon type="close" style="font-size: 12px; color: #999" />
            </span>
        </button>
        <Modal :visible.sync="modal.visible" :width="800" :title="modal.title" :fullscreen.sync="modal.fullscreen"
            :switchFullscreen="modal.switchFullscreen" :cancelText="cancelText" :okText="okText" @ok="Ok">
            <a-form :form="form">
                <a-card :bordered="false" :bodyStyle="{ padding: '0 50px' }" v-for="(item, index) in dataSource"
                    :key="index">
                    <a-row :gutter="[16, 16]">
                        <a-col :span="6">
                            <DictSelectTag style="width：100%" :dataList="typeData" :allowClear="false"
                                valueFeild="fieldClassName" textFeild="fieldNotes" dataType="static" type="select"
                                v-model="item.fieldDesc" @change="onchange"></DictSelectTag>
                        </a-col>
                        <a-col :span="6">
                            <!-- 根据字段类型 分类显示对应判断条件 -->
                            <DictSelectTag style="width：100%" :allowClear="false" :disabled="item.fieldDesc ? false : true"
                                type="select" v-model="item.sql" dictCode="projectFilterType"
                                @change="onchangeSql(arguments, item, index)"></DictSelectTag>
                        </a-col>
                        <a-col :span="12" style="position: relative">
                            <KFormItem :record="filterRecords(item, index)" :formConfig="{}" @change="getValue"></KFormItem>
                            <a-icon type="minus-circle" class="search_delIcon" @click="onDelete(item.key)" />
                        </a-col>
                    </a-row>
                </a-card>
            </a-form>
            <div style="padding: 0 50px">
                <a-icon type="plus-circle" class="search_addIcon" @click="handleAdd" />
            </div>
        </Modal>
    </div>
</template>   
<script>
import Modal from '../Modal/index.vue'
import DictSelectTag from '../DictSelectTag/index.vue'

export default {
    name: "TableSearch",
    components: {
        Modal,
        DictSelectTag,
        KFormItem: () => import('../FormItem/index.vue')
    },
    props: {
        // 按钮文字
        cancelText: {
            type: String,
            default: '取消'
        },
        okText: {
            type: String,
            default: '筛选'
        },
        // 请求url
        searchUrl: {
            type: String,
            require: false,
            default: '/dealersystem/sysMetadataFields/getFormFieldsByObjectClass'
        },
        //请求参数
        param: {
            type: Object,
            require: true,
            default: () => {
                return {}
            }
        }
    },
    data() {
        return {
            modal: {
                visible: false,
                title: '设置筛选',
                fullscreen: false, // 是否全屏弹窗，当全屏时无论如何都会禁止 body 滚动。可使用 .sync 修饰符
                switchFullscreen: true //是否允许切换全屏（允许后右上角会出现一个按钮）
            },
            form: this.$form.createForm(this),
            // 表格筛选的字段
            typeData: [],
            dataLen: 0,
            // 自定义选框集合（最好封装）
            valuesData: [],
            // 筛选条列
            dataSource: [
                {
                    key: 0,
                    fieldDesc: null,
                    controlType: null,
                    sql: null,
                    values: null
                }
            ]
        }
    },
    watch: {
        modal: {
            handler(newV) {
                // console.log(newV)
            },
            deep: true
        },
        dataSource: {
            handler(newV) {
                // console.log(newV)
                this.dataLength(newV)
            },
            deep: true
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        // 请求数据
        getData() {
            this.param.whetherSearch = true;
            this.$post(this.searchUrl, this.param).then((res) => {
                if (res.statusCode !== 300) {
                    this.typeData = res
                }
            })
        },
        // 弹窗
        handleSearch() {
            this.modal.visible = true
        },
        // 有效条件数量
        dataLength(data) {
            let len = data.filter((t) => t.values).length
            let len2 = data.filter(
                (t) => t.values == null && t.sql && (t.sql.trim() === 'is null' || t.sql.trim() === 'is not null')
            ).length
            this.dataLen = len + len2
        },
        // 清除查询数据，初始化
        delData() {
            this.dataSource = [
                {
                    key: 1,
                    fieldDesc: null,
                    controlType: null,
                    dictionariesCode: null,
                    sql: null,
                    values: null
                }
            ]
            this.clearSesarchData()
        },
        // 添加条件列
        handleAdd() {
            const newData = {
                key: this.dataSource.length + 1,
                fieldDesc: null,
                controlType: null,
                dictionariesCode: null,
                sql: null,
                values: null
            }
            this.dataSource = [...this.dataSource, newData]
        },
        // 删除条件
        onDelete(key) {
            if (this.dataSource.length === 1) {
                this.$message.warn('筛选需要至少保留一个条件')
                return
            }
            const arr = [...this.dataSource]
            this.dataSource = arr.filter((item) => item.key !== key)
        },
        // 第一个字段选择框，获取对应的类型
        onchange(fieldDesc) {
            let val = null
            let dictionariesCode = null
            for (const t of this.typeData) {
                if (t.fieldClassName == fieldDesc) {
                    val = t.controlType
                    dictionariesCode = t.dictionariesCode
                    break
                }
            }
            if (val) {
                this.dataSource.forEach((t2) => {
                    if (t2.fieldDesc === fieldDesc) {
                        t2.controlType = val
                        t2.sql = 'equal'
                        t2.dictionariesCode = dictionariesCode
                    }
                })
            }
        },
        // 第二个条件选择框
        onchangeSql(arg, item, index) {
            // console.log(arg[0], item)
            // 切换条件时，清除对应 values 数据
            for (let t of this.dataSource) {
                if (t.fieldDesc === item.fieldDesc) {
                    t.values = null
                    break
                }
            }
            // 清空对应第三动态框显示数据
            let values = item.fieldDesc + index
            this.form.setFieldsValue({
                [values]: null
            })
        },
        // 第三个动态选框，获取返回值
        getValue(value, key) {
            this.dataSource.forEach((t, index) => {
                if (t.fieldDesc + index === key) {
                    t.values = value
                }
            })
        },
        // 根据分类显示对应的表单框
        filterRecords(item, index) {
            let records = {}

            if (item.controlType) {
                records.model = item.fieldDesc + index

                let options = {}
                // 控件选择
                switch (item.controlType) {
                    case 'numberbox': // 数字输入框控件
                        records.type = 'number'
                        options = {
                            disabled: item.fieldDesc ? false : true,
                            placeholder: '请输入'
                        }
                        break
                    case 'datetimebox': // 日期控件
                        records.type = 'date'
                        options = {
                            disabled: item.fieldDesc ? false : true,
                            width: '100%',
                            format: 'YYYY-MM-DD'
                        }
                        if (item.sql === 'timeRange') {
                            // 时间段
                            options.range = true
                        } else {
                            // 单个日期
                            options.placeholder = '请选择日期'
                            options.range = false
                        }
                        break
                    case 'combobox': // 下拉框控件
                        records.type = 'select'
                        options = {
                            disabled: item.fieldDesc ? false : true,
                            allowClear: false
                        }
                        break
                    case 'textarea':
                    case 'textbox': // 输入框控件
                        records.type = 'input'
                        options = {
                            disabled: item.fieldDesc ? false : true,
                            placeholder: '请输入'
                        }
                        break
                }
                // 如果条件 为空（未填写）和 不为空、或者非时间date而选择时间段 时，禁止第三框
                if (
                    item.sql.trim() === 'is null' ||
                    item.sql.trim() === 'is not null' ||
                    (item.controlType !== 'datetimebox' && item.sql.trim() === 'timeRange')
                )
                    options.disabled = true

                // 数据字典
                if (item.dictionariesCode) options.dictCode = item.dictionariesCode
                records.options = options
                // console.log(records)
            } else {
                // 选值默认框
                records = {
                    type: 'input', //动态框类型
                    model: 'default', //绑定值
                    options: {
                        disabled: true,
                        type: '', //内部类型
                        placeholder: '请输入'
                    }
                }
            }

            return records
        },
        // 确认提交查询数据
        Ok() {
            // 转换查询数据
            let requestList = []
            this.dataSource.forEach((t) => {
                let queryParams = {}

                if (t.values !== null) {
                    queryParams.field = t.fieldDesc
                    if (t.sql === 'timeRange') {
                        //时间段，分成两个参数，大于等于开始日期、和小于等于结束日期,[2022-12-5,2022-12-7]
                        t.values.forEach((t2, index) => {
                            let queryParams1 = {}
                            queryParams1.field = t.fieldDesc
                            queryParams1.value = t2 // new Date(t2).getTime()
                            queryParams1.op = index === 0 ? 'greaterorequal' : 'lessorequal'
                            requestList.push(queryParams1)
                        })
                    } else {
                        queryParams.op = t.sql
                        queryParams.value = typeof t.values == 'string' ? t.values.trim() : typeof t.values
                        requestList.push(queryParams)
                    }
                } else {
                    // 条件 为空 和 不为空， 传rb（values 必无值）
                    queryParams.field = t.fieldDesc
                    queryParams.rb = t.sql
                    requestList.push(queryParams)
                }
            })
            console.log('提交', requestList)
            requestList = { filter1: JSON.stringify(requestList) }

            this.$emit('ok', requestList)
        },
        // 清空查询
        clearSesarchData() {
            this.$emit('ok', {})
        }
    }
}
</script> 
<style lang="less" scoped>
.DataSearch {
    display: inline-block;

    .btn_delIcon {
        display: inline-block;
        padding: 0 3px;
        border: 1px solid rgba(221, 221, 221, 0.6);
        border-radius: 2px;
        margin-left: 8px;
        background: rgba(221, 221, 221, 0.6);
        color: #ff8000;
        cursor: pointer;
    }
}

/* 筛选按钮 */
.cu-btn-search {
    line-height: 1.499;
    position: relative;
    display: inline-block;
    font-weight: 400;
    white-space: nowrap;
    text-align: center;
    background-image: none;
    // box-shadow: 0 2px 0 rgb(0 0 0 / 2%);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    touch-action: manipulation;
    height: 32px;
    padding: 0 15px;
    font-size: 14px;
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.65);
    background-color: #fff;
    border: 1px solid #d9d9d9;
}

.cu-btn-search,
.cu-btn-search:hover,
.cu-btn-search:focus {
    margin-right: 10px !important;
}

.cu-btn-search:hover {
    opacity: 0.8;
    color: #40a9ff;
    background-color: #fff;
    border-color: #40a9ff;
}

.search_addIcon {
    font-size: 18px;
    width: 18px;
    color: #737c8c;
}

.search_delIcon {
    position: absolute;
    right: -18px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    width: 18px;
    color: #737c8c;
}

.search_delIcon:hover,
.search_addIcon:hover {
    color: #ff8000;
}

.ant-form-item {
    width: 100%;
    height: 32px;
    line-height: 32px;
    margin: 0 !important;

    /deep/ .ant-form-item-control {
        line-height: 32px;
    }
}
</style>TableSearch