import Vue from 'vue'
export default new Vue()

/**
 * 获取字符串长度，英文字符 长度1，中文字符长度2
 * @param {*} str
 */
export const getStrFullLength = (str = '') =>
    str.split('').reduce((pre, cur) => {
        const charCode = cur.charCodeAt(0)
        if (charCode >= 0 && charCode <= 128) {
            return pre + 1
        }
        return pre + 2
    }, 0)


/**
* 截取字符串，根据 maxLength 截取后返回
* @param {*} str
* @param {*} maxLength
*/

export const cutStrByFullLength = (str = '', maxLength) => {
    let showLength = 0
    return str.split('').reduce((pre, cur) => {
        const charCode = cur.charCodeAt(0)
        if (charCode >= 0 && charCode <= 128) {
            showLength += 1
        } else {
            showLength += 2
        }
        if (showLength <= maxLength) {
            return pre + cur
        }
        return pre
    }, '')
}
// 表格可拖拽列的函数

export function resizeableTitle(h, props, children, columns) {
    const draggingMap = {}
    columns.forEach((col) => {
        col.ellipsis = true
        draggingMap[col.key] = col.width
    })
    const draggingState = Vue.observable(draggingMap)
    let thDom = null
    const { key, ...restProps } = props
    let index = 0
    const col = columns.find((col, colIndex) => {
        const k = col.dataIndex || col.key
        index = colIndex
        return k === key
    })
    if (!col?.width) {
        return <th {...restProps}> {children} </th>
    }
    const onDrag = (x) => {
        draggingState[key] = 0
        col.width = Math.max(x, 1)
    }
    const onDragstop = () => {
        draggingState[key] = thDom.getBoundingClientRect().width
    }
    return (
        <th {...restProps} v-ant-ref={(r) => (thDom = r)} width={col.width} class="resize-table-th">
            {children}
            <vue-draggable-resizable
                key={col.key}
                class="table-draggable-handle"
                w={10}
                x={
                    draggingState[key] ||
                    (col?.width && typeof col?.width === 'string' ? Number(col?.width.split('px')[0]) : col?.width)
                }
                z={1}
                axis="x"
                draggable={true}
                resizable={false}
                onDragging={onDrag}
                onDragstop={onDragstop}
            ></vue-draggable-resizable>
        </th>
    )
}

export function getClass(ele) {
    let data = {}
    if (ele.data) {
        data = ele.data
    } else if (ele.$vnode && ele.$vnode.data) {
        data = ele.$vnode.data
    }
    const tempCls = data.class || {}
    const staticClass = data.staticClass
    let cls = {}
    staticClass &&
        staticClass.split(' ').forEach(c => {
            cls[c.trim()] = true
        })
    if (typeof tempCls === 'string') {
        tempCls.split(' ').forEach(c => {
            cls[c.trim()] = true
        })
    } else if (Array.isArray(tempCls)) {
        classNames(tempCls)
            .split(' ')
            .forEach(c => {
                cls[c.trim()] = true
            })
    } else {
        cls = { ...cls, ...tempCls }
    }
    return cls
}

export function getStyle(ele, camel) {

    getClass(ele)

    let data = {}
    if (ele.data) {
        data = ele.data
    } else if (ele.$vnode && ele.$vnode.data) {
        data = ele.$vnode.data
    }

    // update-begin-author:sunjianlei date:20200303 for: style 和 staticStyle 可以共存
    let style = data.style || {}
    let staticStyle = data.staticStyle
    staticStyle = staticStyle ? objectCamelize(data.staticStyle) : {}
    // update-end-author:sunjianlei date:20200303 for: style 和 staticStyle 可以共存

    if (typeof style === 'string') {
        style = parseStyleText(style, camel)
    } else if (camel && style) {
        // 驼峰化
        style = objectCamelize(style)
    }
    return { ...staticStyle, ...style }
}


/**
 * 字符串转成对象
 * @param master  主数组
 * @param sub  子数组
 * @param key  标识
 */

export function getObjectByString(params, symbol1, symbol2) {
    if (!params) return {}
    params = params.split(symbol1)
    var obj = {}
    for (var i = 0; i < params.length; i++) {
        var param = params[i].split(symbol2)
        obj[param[0]] = param[1]
    }
    params.map((item) => (obj[item.split(symbol2)[0]] = item.split(symbol2)[1]))
    return obj
}