  
<template>
    <a-modal ref="modal" :class="getClass(modalClass)" :style="getStyle(modalStyle)" v-dialogDrag:{dialogDrag}="dialogDrag"
        :bodyStyle="bodyStyle" :visible="visible" v-bind="_attrs" v-on="$listeners" @ok="handleOk" @cancel="handleCancel"
        :maskClosable="false" :cancelText="cancelText" :okText="okText" :z-index="zIndex">
        <slot></slot>
        <template v-if="!isNoTitle" slot="title">
            <a-row class="t-modal-title-" type="flex">
                <a-col class="left">
                    {{ title }}
                </a-col>
                <a-col v-if="switchFullscreen" class="right fullscreenBox"
                    :class="[fullscreen ? 'fullscreen' : '', 'right']">
                    <a-button class="ant-modal-close ant-modal-close-x" ghost type="link" :icon="fullscreenButtonIcon" />
                </a-col>
                <a-col v-if="!switchFullscreen && fullscreen" class="fullscreen fullscreenBox"
                    style="display: none;"></a-col>
            </a-row>
        </template>

        <!-- 处理 scopedSlots -->
        <template v-for="slotName of scopedSlotsKeys" :slot="slotName">
            <slot :name="slotName"></slot>
        </template>

        <!-- 处理 slots -->
        <template v-for="slotName of slotsKeys" v-slot:[slotName]>
            <slot :name="slotName"></slot>
        </template>
    </a-modal>
</template>
  
<script>

import { getClass, getStyle } from "../utils/util";

export default {

    name: "Modal",
    props: {
        title: String,
        // 可使用 .sync 修饰符
        visible: Boolean,
        // 是否全屏弹窗，当全屏时无论如何都会禁止 body 滚动。可使用 .sync 修饰符
        fullscreen: {
            type: Boolean,
            default: false,
        },
        // 是否允许切换全屏（允许后右上角会出现一个按钮）
        switchFullscreen: {
            type: Boolean,
            default: false,
        },
        // 点击确定按钮的时候是否关闭弹窗
        okClose: {
            type: Boolean,
            default: true,
        },
        // 可拖拽
        dialogDrag: {
            type: Boolean,
            default: true,
        },
        cancelText: {
            type: String,
            default: "取消",
        },
        okText: {
            type: String,
            default: "确定",
        },
        zIndex: {
            type: Number,
            default: 1000
        }
    },
    data() {
        return {
            // 内部使用的 slots ，不再处理
            usedSlots: ["title"],
            // 实际控制是否全屏的参数
            innerFullscreen: this.fullscreen,
            bodyStyle: {}
        };
    },
    computed: {
        // 一些未处理的参数或特殊处理的参数绑定到 t-modal 上
        _attrs() {
            let attrs = { ...this.$attrs };
            // 如果全屏就将宽度设为 100%
            if (this.innerFullscreen) {
                attrs["width"] = "100%";
            }
            return attrs;
        },
        modalClass() {
            return {
                "t-modal-box": true,
                fullscreen: this.innerFullscreen,
                "no-title": this.isNoTitle,
                "no-footer": this.isNoFooter,
                mobile: this.isMobile,
            };
        },
        modalStyle() {
            let style = {};
            // 如果全屏就将top设为 0
            if (this.innerFullscreen) {
                style["top"] = "0";
                // 全屏时，中间内容超出呈现范围滚动
                this.bodyStyle = {
                    overflow: 'auto',
                    height: `calc(100VH - ${this.isNoFooter ? 0 : 53}px - ${this.isNoTitle ? 0 : 43}px )`
                }
            } else {
                this.bodyStyle = {
                    overflow: 'auto',
                    maxHeight: '600px'
                }
            }
            return style;
        },
        isNoTitle() {
            return this.title && this.allSlotsKeys.includes("title");
        },
        isNoFooter() {
            return this._attrs["footer"] === null;
        },
        slotsKeys() {
            return Object.keys(this.$slots).filter(
                (key) => !this.usedSlots.includes(key)
            );
        },
        scopedSlotsKeys() {
            return Object.keys(this.$scopedSlots).filter(
                (key) => !this.usedSlots.includes(key)
            );
        },
        allSlotsKeys() {
            return this.slotsKeys.concat(this.scopedSlotsKeys);
        },
        // 切换全屏的按钮图标
        fullscreenButtonIcon() {
            return this.innerFullscreen ? "fullscreen-exit" : "fullscreen";
        },
    },
    watch: {
        visible() {
            if (this.visible) {
                this.innerFullscreen = this.fullscreen;
            }
        },
        innerFullscreen(val) {
            this.$emit("update:fullscreen", val);

        },
    },
    destroyed() {
        // console.log('destroyed')
    },
    methods: {
        getClass(clazz) {
            return { ...getClass(this), ...clazz };
        },
        getStyle(style) {
            return { ...getStyle(this), ...style };
        },
        close() {
            this.$emit("update:visible", false);
        },
        handleOk() {
            if (this.okClose) {
                this.close();
            }
        },
        handleCancel() {
            this.close();
        },
        /** 切换全屏 */
        toggleFullscreen() {
            this.innerFullscreen = !this.innerFullscreen;
        },
    },
};
</script>
  
   