<script>
import { Menu, Icon, Input } from 'ant-design-vue'
const { Search } = Input
const { Item, ItemGroup, SubMenu } = Menu

export default {
    name: 'Tree',
    props: {
        dataSource: {
            type: Array,
            required: true
        },
        openKeys: {
            type: Array,
            default: () => []
        },
        search: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        handlePlus(item) {
            this.$emit('add', item)
        },
        renderSearch() {
            return (<
                Search placeholder="input search text"
                style="width: 100%; margin-bottom: 1rem" /
            >
            )
        },
        renderIcon(icon) {
        },
        renderMenuItem(item) {
            return (
                < Item key={item.key} >
                    {this.renderIcon(item.icon)} {item.title}
                    < a class="btn" style="width: 20px;z-index:1300" {... { on: { click: () => this.handlePlus(item) } }} >
                        < a-icon type="plus" />
                    </a>
                </Item>
            )
        },
        renderItem(item) {
            return (item.children ? this.renderSubItem(item, item.key) : this.renderMenuItem(item, item.key))
        },
        renderSubItem(item, key) {
            const childrenItems = item.children && item.children.map(o => {
                return this.renderItem(o, o.key)
            })
            const title = (
                < span slot="title" >
                    {this.renderIcon(item.icon)}
                    <span > {item.title} </span>
                </span>
            )
            if (item.group) {
                return this.renderItemGroup(item)
            }
            return (<SubMenu key={key} > {title} {childrenItems}
            </SubMenu>
            )

        },
    },
    created() {
        this.localOpenKeys = this.openKeys.slice(0)
    },
    render() {
        const { dataSource, search } = this.$props
        const list = dataSource.map(item => {
            return this.renderItem(item)
        })
        return (
            <div class="tree-wrapper" >
                {search ? this.renderSearch() : null}
                <Menu
                    mode="inline"
                    class="custom-tree"
                    {...
                    {
                        on: {
                            click: item => this.$emit('click', item),
                            'update:openKeys': val => { this.localOpenKeys = val }
                        }
                    }}
                    openKeys={this.localOpenKeys} > {list}
                </Menu>
            </div>
        )


    }
}
</script>
